"use client"

import { useCartContext } from "./CartContext";
import Link from "next/link"

export default function CartPage() {
    const { cart, updateQuantity, removeFromCart, clearCart } = useCartContext();

    if (cart.length === 0) {
        return (
        <div className="p-6 text-center">
            <h1 className="text-3xl font-bold mb-4">Корзина пуста</h1>
            <p className="text-gray-600 mb-6">Добавьте товары из каталога</p>
            <Link 
            href="/"
            className="bg-blue-600 text-white px-6 py-3 rounded-lg hover:bg-blue-700 transition inline-block"
            >
            ← Вернуться в каталог
            </Link>
        </div>
        );
    }

    return (
        <div className="p-6 max-w-4xl mx-auto">
            <div className="flex justify-between items-center mb-6">
                {/* <h1 className="text-3xl font-bold">Корзина ({getItemCount()} шт.)</h1> */}
                <Link 
                href="/"
                className="text-blue-600 hover:underline"
                >
                ← Продолжить покупки
                </Link>
            </div>

            <div className="space-y-4 mb-6">
                {cart.map((item) => (
                <div key={item.id} className="flex gap-4 p-4 border rounded-lg">
                    {item.image ? (
                    <img 
                        src={item.image} 
                        alt={item.name}
                        className="w-24 h-24 object-cover rounded"
                    />
                    ) : (
                    <div className="w-24 h-24 bg-gray-200 rounded flex items-center justify-center">
                        <span className="text-gray-400 text-xs">No image</span>
                    </div>
                    )}

                    <div className="flex-1">
                    <h3 className="font-semibold text-lg">{item.name}</h3>
                    {/* {item.category && (
                        <p className="text-sm text-gray-600">{item.category}</p>
                    )} */}
                    <p className="text-blue-600 font-bold mt-1">
                        {/* {item.price.toLocaleString('ru-RU')} ₽ */}
                    </p>
                    </div>

                    <div className="flex flex-col items-end justify-between">
                    <button
                        // onClick={() => removeFromCart(item.id)}
                        className="text-red-500 hover:text-red-700 text-sm"
                    >
                        ✕ Удалить
                    </button>

                    <div className="flex items-center gap-2">
                        <button
                        onClick={() => updateQuantity(item.id, item.quantity - 1)}
                        className="w-8 h-8 bg-gray-200 rounded hover:bg-gray-300"
                        >
                        −
                        </button>
                        <span className="w-12 text-center font-semibold">
                        {item.quantity}
                        </span>
                        <button
                        onClick={() => updateQuantity(item.id, item.quantity + 1)}
                        className="w-8 h-8 bg-gray-200 rounded hover:bg-gray-300"
                        >
                        +
                        </button>
                    </div>

                    <p className="font-bold text-lg">
                        {/* {(item.price * item.quantity).toLocaleString('ru-RU')} ₽ */}
                    </p>
                    </div>
                </div>
                ))}
            </div>

            <div className="border-t pt-6">
                <div className="flex justify-between items-center mb-4">
                <span className="text-2xl font-bold">Итого:</span>
                <span className="text-3xl font-bold text-blue-600">
                    {/* {getTotal().toLocaleString('ru-RU')} ₽ */}
                </span>
                </div>

                <div className="flex gap-4">
                <button
                    // onClick={clearCart}
                    className="flex-1 bg-gray-200 text-gray-700 py-3 rounded-lg hover:bg-gray-300 transition"
                >
                    Очистить корзину
                </button>
                <button
                    className="flex-1 bg-green-600 text-white py-3 rounded-lg hover:bg-green-700 transition"
                >
                    Оформить заказ
                </button>
                </div>
            </div>
        </div>
  );

  

}